* {
  font-family: Arial, Helvetica, sans-serif;

}

body {
  padding: 40px;
}

input {
  width: 100%;
  color: gray;
  font-size: 1.2rem;
}

.card {
  align-items: center;
  border: 1px solid gray;
  border-radius: 6px ;
  overflow: hidden;
  margin-bottom: 10px;
  margin-right: 10px;

}

.card p {

  padding:5px ;
  text-align: center;

}

.card img {
  /* max-height: 200px; */
  width: 100%;
}

.card-grid {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}